// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

*{padding: 0;margin: 0;border: 0}
*,*:before, *:after{-moz-box-sizing: border-box;-webkit-box-sizing: border-box;box-sizing: border-box}
:focus, :active{outline: none;}
*:focus, *:active{outline: none;}
nav, footer, header, aside{display: block;}
html, body{width: 100%;font-size: 100%;line-height: 1;font-size: 14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}
input, button, textarea{font-family: inherit; background: none;}
input::-ms-clear{display: none;}
button{cursor:pointer;}
button::-moz-focus-inner{padding: 0; border: 0}
a{color: inherit}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align:top;display:block;max-width:100%;}
h1,h2,h3,h4,h5,h6{font-size: inherit; font-weight: 400; margin: 0;}
ul{margin: 0}
input[type=text]::-ms-clear {display:none;width:0;height:0}
input[type=text]::-ms-reveal {display:none;width:0;height:0}
input::-webkit-search-decoration,input::-webkit-search-cancel-button,input::-webkit-search-results-button,input::-webkit-search-results-decoration{display:none}
@media (prefers-reduced-motion: reduce){*{animation-duration: 0.01ms !important;animation-iteration-count: 1 !important;transition-duration: 0.01ms !important;scroll-behavior: auto !important;}}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}
textarea{resize: none}

$harmonia: "Harmonia";

.page {
	font-size: 1px;
	overflow-x: hidden;
	&._lock{
		overflow: hidden;
	}
	@media only screen and (max-width: 1420px){
		font-size: .07vw;
	}
	@media only screen and (max-width: 991px){
		font-size: 1px;
	}
	@media only screen and (max-width: 359px){
		font-size: 0.277vw;
	}
}
.page__body {
	font-family: $harmonia;
	font-size: 20rem;
	line-height: 1.3;
	font-weight: 400;
	background: #3f3f3f url('../img/main.jpg') no-repeat center / cover;
  color: #fff;
	//color: $dark;
	overflow: hidden;
	position: relative;
	left: 0;
	right: 0;
	@media screen and (max-width: 991px) {
		background-position: center 25rem;
	}
	@media screen and (max-width: 767px) {
		background-position: calc(50% + 196rem) 0;
	}
}
.container {
	width: 100%;
	max-width: 1640rem;
	margin: 0 auto;
	padding: 0 100rem;
	@media screen and (max-width: 991px){
		max-width: 768rem;
		padding: 0 20rem;
	}
	@media screen and (max-width: 767px){
		max-width: 360rem;
		padding: 0 15rem;
	}
}
.title{
	font-weight: 700;
	font-size: 70rem;
	line-height: 1;
	text-align: center;
	@media screen and (max-width: 991px) {
		font-size: 32rem;
		line-height: 36rem;
	}
	@media screen and (max-width: 767px) {
		font-size: 25rem;
		line-height: 30rem;
	}
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 58rem;
  &__logo {}
  &__phone {
    &-item {
      font-weight: 700;
      font-size: 25rem;
      line-height: 1;
      &:nth-child(n+2){
        margin-top: 3rem;
      }
    }
  }
}

.main {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 80rem;
  padding-bottom: 160rem;
  &__column {
    &:nth-child(1){
      flex: 0 0 58.39%;
      max-width: 58.39%;
    }
    &:nth-child(2){
      flex: 0 0 40.21%;
      max-width: 40.21%;
    }
  }
}
.tile {
  background-color: #232323;
  padding: 35rem 18rem 35rem 40rem;
  border-radius: 20rem;
  box-shadow: 4rem 4rem 20rem rgba(0, 0, 0, 0.25);
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    font-size: 30rem;
  }
  &__filter {
    font-size: 20rem;
    color: #ccc;
  }
  &__body {
    font-size: 20rem;
    color: #ccc;
    margin-top: 35rem;
  }
  &__columns {
    margin-top: 18rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-item {
      display: block;
      &:nth-child(1){
        width: 240rem;
      }
      &:nth-child(2){
        width: 257rem;
      }
      &:nth-child(3){
        width: 220rem;
      }
      &:nth-child(4){
        text-align: right;
        width: 39rem;
      }
    }
  }
  &__line{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 18rem;
    padding-bottom: 18rem;
    border-bottom: 1rem solid #3f3f3f;
  }
  &__lines {
    &-item {
      position: relative;
      padding-left: 40rem;
      display: block;
      &:nth-child(1){
        width: 240rem;
      }
      &:nth-child(2){
        width: 257rem;
      }
      &:nth-child(3){
        width: 220rem;
      }
      &:nth-child(4){
        text-align: right;
        width: 39rem;
      }
      &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        display: inline-block;
        width: 30rem;
        height: 30rem;
      }
    }
    &_date {
      &::before{
        background: url('../img/date.svg') no-repeat center / contain;
      }
    }
    &_student {
      &::before{
        background: url('../img/person.png') no-repeat center / contain;
      }
    }
    &_teacher {
      &::before{
        background: url('../img/person_1.png') no-repeat center / contain;
      }
    }
    &_room {
      padding-left: 0;
      font-weight: 700;
      &::before{
        display: none;
      }
    }
  }
  &__subtitle {
    font-size: 18rem;
    line-height: 24rem;
    color: #ccc;
    margin-top: 10rem;
  }
  &__parameters {
    margin-top: 18rem;
    &-item {
      display: flex;
      align-items: center;
      &:nth-child(n+2){
        margin-top: 24rem;
      }
    }
    &-name {
      display: block;
      width: 170rem;
    }
  }
  &__add-button {
    border-radius: 20rem;
    border: 1px dashed #F7D584;
    font-size: 20rem;
    line-height: 1;
    font-weight: 700;
    width: 270rem;
    margin-top: 35rem;
    padding: 25rem;
    color: #ccc;
  }
}

.control-buttons {
  margin-top: 31rem;
  margin-left: 40rem;
}
.control-buttons button{
  color: #ccc;
  font-weight: 700;
  font-size: 20rem;
  line-height: 1;
}
.delete {
  margin-left: 40rem;
}
.navigation {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  &__list {
    display: flex;
    align-items: center;
    background-color: #232323;
    border-radius: 40rem 40rem 0 0
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 270rem;
    height: 80rem;
    text-transform: uppercase;
    &:nth-child(2){
      border-left: 1px solid #F7D584;
      border-right: 1px solid #F7D584;
    }
    &._active{
      color: #fff;
      background-color: #3f3f3f;
      font-weight: 700;
    }
  }
}
a:hover{
  color: #f7d584;
  text-decoration: underline;
}